.profile {
  border: 2px solid black;
  display: block;
  width: 280px;
 overflow: hidden;
  border-radius: 10px;
   margin-left: auto;
  margin-right: auto;

}
.avatar {
    display: block;
    border-radius: 50%;
    width: 150px;
margin: 10px auto;
border: 1px  rgb(183, 190, 190) solid;

}
.description {
    margin-left: auto;
    margin-right: auto;
}
.name {
    margin: 0;
    font-size: 25px;
    font-weight: 700;
    text-align: center;
     
}
.tag, .quantity {
font-size: 16px;

text-align: center;
font-weight: 600;
}

.quantity {
    color: black;
}
.stats {
background-color: rgb(183, 190, 190);
display: flex;
list-style-type: none;
margin: 0;
padding: 0;
justify-content: space-around;

}

.element {
    border: 1px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-top: 10px;
    padding-bottom: 10px;
  
}
.label {
    color: rgb(92, 100, 100);
}
.location {
    text-align: center;
     color: rgb(92, 100, 100);
}


